import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { KlaimRoutingModule } from './klaim-routing.module';
import { ListComponent } from './list/list.component';
import { AddComponent } from './add/add.component';
import { EditComponent } from './edit/edit.component';
import { NgZorroAntdModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { DocumentModule } from 'src/app/components/document/document.module';


@NgModule({
  declarations: [ListComponent, AddComponent, EditComponent],
  imports: [
    CommonModule,
    KlaimRoutingModule,
    NgZorroAntdModule,
    ReactiveFormsModule,
    FormsModule,
    PerfectScrollbarModule,
    DocumentModule

],
providers:
[
    FormBuilderTypeSafe
],
})
export class KlaimModule { }
