import { Component, OnInit } from '@angular/core';
import { GaKlaimItemService, GaKlaimKategoriService, GaKlaimPengajuanService, DirektoriVPegawaiPosisiService, DirektoriPegawaiKeluargalistService, DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { ActivatedRoute, Router } from '@angular/router';
import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { Klaim_pengajuan, Pegawai, V_pegawai_posisi, Klaim_kategori, Pegawai_keluarga, Klaim_item } from 'src/app/sdk/hr/models';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';


@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private gaKlaimItemService: GaKlaimItemService,
        private gaKlaimKategoriService: GaKlaimKategoriService,
        private gaKlaimPengajuanService: GaKlaimPengajuanService,
        private toolsLogFlowService: ToolsLogFlowService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private direktoriPegawaiKeluargalistService: DirektoriPegawaiKeluargalistService,
        private spinner: NgxSpinnerService,
        private pegawaiDetailService: PegawaiDetailService,
        private routerActive: ActivatedRoute,
        private router: Router,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private standarApprovalService: StandarApprovalService,
    ) { }

    id_klaim_pengajuan: string = ''
    dataKlaim: Klaim_pengajuan = {}
    dataPegawai: Pegawai = {}
    dataPosisi: V_pegawai_posisi = {}
    dataKategori: Klaim_kategori = {}
    DataKeluarga: Pegawai_keluarga = {}
    dataItem: Klaim_item[] = []
    id_pegawai: string = ''
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
                this.id_pegawai = data;
            }
        )
        this.routerActive.paramMap.subscribe(
            params => {
                if (params.get('id')) {
                    this.id_klaim_pengajuan = params.get('id')
                }

                this.getDetailKlaim()
                this.getKlaimItem()
                this.getLog()
            }
        )

        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getDetailKlaim()
                    this.getKlaimItem()
                    this.getLog()
                }
            }
        )

    }

    params: standarFlow = null;
    id_data_flow: string = ''
    getDetailKlaim() {
        this.spinner.show()
        this.gaKlaimPengajuanService.getGaKlaimPengajuansId(this.id_klaim_pengajuan).subscribe(
            data => {
                this.dataKlaim = data.data;
                this.getPegawai(data.data.id_pegawai);
                this.getPosisi(data.data.id_pegawai);
                this.getKeluarga(data.data.id_pegawai_keluarga);
                this.spinner.hide()

                this.params = {
                    id: data.data.id_klaim_pengajuan,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_klaim_pengajuan"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()
            }
        )
    }

    getPegawai(id_pegawai: string) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(id_pegawai).subscribe(
            data => {
                this.dataPegawai = data.data;
                this.getKlaimKategori(data.data.id_klaim_kategori)
            }
        )
    }

    getKlaimKategori(id_klaim_kategori) {
        this.gaKlaimKategoriService.getGaKlaimKategorisId(id_klaim_kategori).subscribe(
            data => {
                this.dataKategori = data.data
            }
        )
    }

    getPosisi(id_pegawai: string) {
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisisId(id_pegawai).subscribe(
            data => {
                this.dataPosisi = data.data;
            }
        )
    }
    getKeluarga(id_pegawai_keluarga: string) {
        this.direktoriPegawaiKeluargalistService.getDirektoriPegawaiKeluargalistsId(id_pegawai_keluarga).subscribe(
            data => {
                this.DataKeluarga = data.data;
            }
        )
    }


    getKlaimItem() {
        this.gaKlaimItemService.getGaKlaimItems(JSON.stringify({ id_klaim_pengajuan: this.id_klaim_pengajuan })).subscribe(
            res => {
                this.dataItem = res.data;
                this.spinner.hide()
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_klaim_pengajuan })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'klaim', 'list'])
    }

}
