import { Component, OnInit } from '@angular/core';
import { GaKlaimPengajuanService, GaKlaimPengaturanService, DirektoriPegawaiKeluargaService, DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { GaKlaimItemService } from 'src/app/sdk/hr/services/ga-klaim-item.service';
import { Pegawai_keluarga, Klaim_pengajuan, Klaim_pengaturan } from 'src/app/sdk/hr/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';
import { DirektoriPegawaiKeluargalistService } from 'src/app/sdk/hr/services/direktori-pegawai-keluargalist.service';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private gaKlaimPengajuanService: GaKlaimPengajuanService,
        private gaKlaimPengaturanService: GaKlaimPengaturanService,
        private gaKlaimItemService: GaKlaimItemService,
        private direktoriPegawaiKeluargalistService: DirektoriPegawaiKeluargalistService,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private pegawaiDetailService: PegawaiDetailService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,

    ) { }

    id_pegawai: string = ''
    id_klaim_pengajuan: string = ''
    pengajuan: Klaim_pengajuan = {}

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data;
            this.getPreeData()
            this.form()
            this.beforeCreate()
        })
    }

    listKlaimPengaturan: Klaim_pengaturan[] = []
    listKeluarga: Pegawai_keluarga[] = []
    getPreeData() {
        this.gaKlaimPengaturanService.getGaKlaimPengaturans(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(data => {
            this.listKlaimPengaturan = data.data
        })

        this.direktoriPegawaiKeluargalistService.getDirektoriPegawaiKeluargalists(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(data => {
            this.listKeluarga = data.data
        })
    }

    beforeCreate() {
        this.gaKlaimPengajuanService.postGaKlaimPengajuans(this.pengajuan).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.id_klaim_pengajuan = data.data.id_klaim_pengajuan
                console.log(data.data)
                this.getItem()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di load data')
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    id_klaim_pengaturan: string = ''
    nilai: string = ''
    tambahItem() {
        if (this.id_klaim_pengaturan == '' || this.id_klaim_pengaturan == null) {
            this.notification.blank(
                'Notification Title',
                'This is the content of the notification. This is the content of the notification. This is the content of the notification.',
            )
            return false
        }

        this.gaKlaimItemService.postGaKlaimItems({
            id_klaim_pengajuan: this.id_klaim_pengajuan,
            id_klaim_pengaturan: this.id_klaim_pengaturan, nilai: this.nilai
        }).subscribe(res => {
            this.getItem()
        })
    }


    listItem: any[] = []
    getItem() {
        let filter = {
            id_klaim_pengajuan: this.id_klaim_pengajuan,
        }
        this.gaKlaimItemService.getGaKlaimItems(JSON.stringify(filter)).subscribe(
            res => {
                this.listItem = res.data
            })
    }
    deleteTanggal(id) {
        this.spinner.show()
        this.gaKlaimItemService.deleteGaKlaimItemsId(id).subscribe(data => {
            this.getItem()
            this.spinner.hide()
        })
    }


    dataForm: FormGroupTypeSafe<Klaim_pengajuan>
    form() {
        this.dataForm = this.fb.group<Klaim_pengajuan>({
            keterangan_klaim: new FormControl(''),
            id_klaim_pengajuan: new FormControl('', [Validators.required]),
            id_pegawai_keluarga: new FormControl(''),
            status: new FormControl(''),
            nomor: new FormControl(''),
            id_data_flow: new FormControl(''),
            id_data_flow_detail: new FormControl(''),
            posisi: new FormControl(''),
            tanggal_berobat: new FormControl(''),
            tanggal_pengajuan: new FormControl(''),
            nilai: new FormControl(''),
            validasi: new FormControl(''),
            nama_rumah_sakit: new FormControl(''),
            alamat_rumah_sakit: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_pegawai: new FormControl(this.id_pegawai),
            id_dokumen: new FormControl(''),
            id_user: new FormControl(''),
        })
    }

    getValue(key) {
        return this.dataForm.get(key).value
    }

    getNilai(key) {
        return (key).value
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataForm.patchValue({ id_pegawai: this.id_pegawai });

        this.pengajuan = this.dataForm.value
        this.gaKlaimPengajuanService
            .putGaKlaimPengajuansId({
                id: this.id_klaim_pengajuan,
                body: this.pengajuan,
            })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'klaim', 'list'])
    }

}
